








































































































  import drawQrcode from '../../common/qrcode.min.js'
  
	export default {
		data() {
			return {
        id: '',
				qrcode: '', // 平台二维码
        author: '',
        wechatFlag: false,
        wechatStr: '',
        wechatQrCode: '',
        essayContent: null,
        qrcodeUrl: ''
			}
		},
    computed: {
      config() {
        return this.$store.state.config
      },
      userInfo() {
        return this.$store.state.userInfo
      },
      defaultAvatar() {
        return '../../static/images/avatar.png'
      },
      isAuthor() {
        return this.author
      }
    },
		onLoad(query) {
      this.id = query.eId
			this.qrcode = this.config.wx_qr_code
      this.author = this.userInfo.id
      this.$http({
        url: this.$apis.materialContent,
        method: 'GET',
        data: {
          id: this.id,
          uid: this.author
        },
        cb: (err, data) => {
          if (!err && data.success === true) {
            this.essayContent = data.data
            this.essayContent.link_content = this.htmlDecode(this.essayContent.link_content)
            this.essayContent.link_content = this.essayContent.link_content.replace(/\<img/gi, '<img style="display:block;max-width:100%;height:auto" ')
            // 生成二维码
            if (this.isAuthor) {
              let url = this.$constant.shareUrl + '?pid=' + this.author
              this.createQrcode(url, 364, (res) => {
                this.qrcodeUrl = res
              })
            } else {
              this.qrcodeUrl = data.data.qr_code
            }
          } else {
            uni.showToast({
            	title: '获取文章详情失败',
              icon: 'none'
            })
          }
        }
      })
		},
		methods: {
      createQrcode(url, w, cb) {
        drawQrcode({
          width: uni.upx2px(w),
          height: uni.upx2px(w),
          canvasId: 'qrcodeCanvas',
          text: url
        })
        setTimeout(() => {
          uni.canvasToTempFilePath({
          	canvasId: 'qrcodeCanvas',
            success: res => {
            	if (typeof cb === 'function') {
            	  cb(res.tempFilePath)
            	}
            }
          })
        }, 300)
      },
      clickZan(item) {
        this.$http({
          url: this.$apis.materialStar,
          method: 'POST',
          data: {
            id: this.id
          },
          cb: (err, data) => {
            if (!err && data.success) {
              item.star = data.data
              item.has_star = !item.has_star
            } else {
              uni.showToast({
              	title: '点赞失败，请稍后再试',
                icon: 'none'
              })
            }
          }
        })
      },
      showWechat(str, url) {
        if (!str && !url) return
        this.wechatStr = str
        this.wechatQrCode = url || ''
        this.wechatFlag = true
      },
      hideWechat() {
        this.wechatFlag = false
      },
      call(num) {
        if (!num) {
          uni.showToast({
          	title: '用户未填写手机号码',
            icon: 'none'
          })
        } else {
          uni.makePhoneCall({
          	phoneNumber: num
          })
        }
      },
      copy(str, type) {
        uni.setClipboardData({
        	data: str,
          success: () => {
            if (type) {
              uni.showModal({
                title: '复制成功',
                content: '文案已复制，请打开链接去分享',
                showCancel: false
              })
            } else {
              uni.showModal({
                title: '复制成功',
                content: '微信已复制，请打开微信查找添加',
                showCancel: false
              })
            }
          },
          fail: () => {
            uni.showModal({
              title: '复制失败',
              content: str,
              showCancel: false
            })
          },
          complete: () => {
          	this.hideWechat()
          }
        })
      },
      read(url) {
        uni.setStorageSync('webSrc', url)
        this.$navigateTo({
        	url: '/pages/web/index'
        })
      },
      htmlDecode(str) {
        return str ? str.replace(/&((g|l|quo)t|amp|#39|nbsp);/g, m => {
          return {
            '&lt;': '<',
            '&amp;': '&',
            '&quot;': '"',
            '&gt;': '>',
            '&#39;': "'",
            '&nbsp;': ' '
          }[m]
        }) : ''
      }
		}
	}
